.experiences-section {
    background-color: #f0f2f5;
    padding: 80px 0;
}

.experiences-container {
    max-width: 1200px;
    margin: 0 auto;
    padding: 0 20px;
}

.experiences-section h2 {
    text-align: center;
    color: #1a1a2e;
    margin-bottom: 10px;
}

.experiences-subtitle {
    text-align: center;
    color: #666;
    margin-bottom: 50px;
    max-width: 700px;
    margin-left: auto;
    margin-right: auto;
}

.testimonials-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 30px;
}

.testimonial-card {
    background-color: #ffffff;
    border-radius: 8px;
    overflow: hidden;
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
}

.testimonial-content {
    padding: 30px;
}

.testimonial-text {
    color: #555;
    font-style: italic;
    line-height: 1.6;
}

.testimonial-author {
    display: flex;
    align-items: center;
    padding: 15px 30px;
    background-color: #f8f8f8;
    border-top: 1px solid #eee;
}

.author-avatar {
    width: 50px;
    height: 50px;
    border-radius: 50%;
    object-fit: cover;
    margin-right: 15px;
}

.author-info h4 {
    margin: 0;
    color: #1a1a2e;
}

.author-info p {
    margin: 5px 0 0;
    color: #666;
    font-size: 0.9rem;
}